@import url(https://fonts.googleapis.com/css2?family=Berkshire+Swash&family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap);
/* FLEX ROWS */
.fl-row-cen-cen {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.fl-row-start-cen {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.fl-row-cen-start {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
}

.fl-row-between-cen {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

/* FLEX COLUMNS */

.fl-col-cen-cen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.fl-col-start-cen {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.fl-col-cen-start {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.fl-col-between-cen {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

/* FLEX SELF-ALIGNS */
.self-start {
  align-self: flex-start;
}
.self-end {
  align-self: flex-end;
}
.w-10 {
  width: 10%;
}
.w-20 {
  width: 20%;
}
.w-25 {
  width: 25%;
}
.w-48 {
  width: 48%;
}
.w-50 {
  width: 50%;
}
.w-75 {
  width: 75%;
}
.w-100 {
  width: 100%;
}
.w-auto {
  width: auto;
}
.min-w-100 {
  min-height: 100vw;
}
.min-h-100 {
  min-height: 100vh;
}

/* PRESETS/GLOBAL STYLES */
:root {
  --chelsea-blue: #034694;
  --azure: #007fff;
  --capri: #00bfff;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html {
  font-size: 16px;
  height: 100%;
}
body {
  color: #f5f5f5;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  min-height: 100%;
}
a {
  color: #00bfff;
  color: var(--capri);
  text-decoration: none;
  font-weight: 500;
}
a:hover {
  color: #007fff;
  color: var(--azure);
  text-decoration: underline;
}
p {
  font-weight: 300;
  margin-top: 1rem;
}
li {
  list-style: none;
}
form input {
  border: 1px solid gray;
  border-radius: 4px;
  background-color: black;
  color: white;
  font-size: 1rem;
  font-weight: 500;
  margin-top: 1rem;
  padding: 1rem 0.5rem;
  outline: 0;
  transition: 0.3s ease;
  width: 100%;
}
form input:focus {
  border: 1px solid transparent;
  border-bottom: 1px solid #007fff;
  border-bottom: 1px solid var(--azure);
  border-radius: 0px;
}
form input:focus::-webkit-input-placeholder {
  color: #00bfff;
  color: var(--capri);
}
form input:focus:-ms-input-placeholder {
  color: #00bfff;
  color: var(--capri);
}
form input:focus::placeholder {
  color: #00bfff;
  color: var(--capri);
}
button {
  border: 1px solid gray;
  border-radius: 50px;
  background-color: black;
  color: white;
  cursor: pointer;
  font-size: 1rem;
  margin-top: 1rem;
  padding: 0.75rem 2rem;
  outline: 0;
  transition: 0.3s ease;
  width: 100%;
}
button:hover {
  border: 1px solid lightgray;
  background-color: rgb(25, 25, 25);
}
button.primary {
  border: 1px solid transparent;
  background-color: #034694;
  background-color: var(--chelsea-blue);
}
button.primary:hover {
  background-color: #007fff;
  background-color: var(--azure);
}
.hr {
  border-top: 1px solid #2e2e2e;
  margin-top: 1rem;
}

.App {
  background-color: black;
  width: 100%;
}
.postic {
  font-family: 'Berkshire Swash', sans-serif;
  font-size: 4rem;
}
.main-page,
.login-page,
.signup-page {
  padding: 2rem;
}
.main-page-heading {
  font-weight: 800;
  margin-bottom: 2rem;
}
.main-login-section {
  margin-top: 2rem;
}
.main-login-section button {
  margin-top: 0.5rem;
}

